import "core-js/modules/es.array.push.js";
import { Notify } from 'vant';
import { signin, signup, verifCode } from "@/api/login.js";
import { hasEmaty } from "@/utils/tool";
import SiHeader from "@/components/SimpleHeader";
import { defineComponent } from "vue";
export default defineComponent({
  name: "UserLogin",
  data() {
    return {
      activeIndex: 0,
      username: "lzo",
      passwd: "456",
      verToken: "",
      verImg: "",
      userCodeVal: ""
    };
  },
  components: {
    SiHeader
  },
  methods: {
    changeTab(index) {
      this.activeIndex = index;
    },
    signin() {
      let params = {
        name: this.username,
        passwd: this.passwd
      };
      if (hasEmaty(params)) {
        return;
      }
      if (this.verToken != this.userCodeVal) {
        Notify('验证码不正确');
        return;
      }
      signin(params).then(res => {
        localStorage.setItem("token", res.token);
        localStorage.setItem("userInfo", JSON.stringify(res.userInfo));
        this.$router.push({
          path: "/index"
        });
      });
    },
    signup() {
      let params = {
        name: this.username,
        passwd: this.passwd
      };
      if (hasEmaty(params)) {
        return;
      }
      signup(params).then(() => {
        this.activeIndex = 0;
      });
    },
    getVerifCode() {
      verifCode().then(res => {
        this.verImg = `data:image/jpeg;base64,${res.base64}`;
        this.verToken = res.token;
      });
    }
  },
  mounted() {
    this.getVerifCode();
  }
});